export class Task {
    public id: number;
    public name: string;
    public completed: boolean;
    public dueDate: string;

    constructor(id: number, name: string, completed: boolean, dueDate: string ){
        this.name = name;
        this.completed = completed;
        this.dueDate = dueDate;
    }
}


